import { Component, OnInit, OnDestroy } from '@angular/core';

import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'exit',
  templateUrl: './exit.component.html',
  styleUrls: ['./exit.component.css']
})
export class ExitComponent implements OnInit, OnDestroy {
  private sub: any;

  score: number;

  title: string;
  subtitle: string;
  text: string;
  imgUrl: string;

  fbSharer: string;
  liSharer: string;

  constructor(private route: ActivatedRoute) { 
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.score = +params['score'];

      this.fbSharer = 'https://www.facebook.com/sharer/sharer.php?u=facs.zivac.ro/bye/' + this.score;
      this.liSharer = 'https://www.linkedin.com/shareArticle?mini=true&url=facs.zivac.ro/bye/' + this.score + '/&title=Testeaz%C4%83-%C8%9Bi%20abilit%C4%83%C8%9Bile%20de%20recunoa%C8%99tere%20a%20microexpresiilor%20%C8%99i%20afl%C4%83%20dac%C4%83%20e%C8%99ti%20un%20bun%20decodificator%20FACS!&summary=&source=';

      if(this.score >= 10) {
        this.title = 'EXPERT în decodificarea microexpresiilor';
        this.subtitle = 'Felicitări! Ești un observator dibace! Ai obținut punctajul maxim în recunoașterea microexpresiilor.';
        this.text = 'Dacă vrei să îți perfecționezi abilitățile de decodificare și să afli informații noi despre limbajul nonverbal și interpretarea microexpresiilor, te invităm să participi la cusul  de formare FACS (Facial Action Coding System) susținut de Trainerul Internațional Dr. Igor Vitale, expert în analiza comportamentală și tehnici FACS. La final vei primi o Diplomă de Participare, sub egida Zivac Group, Internațional Applied Psychology Institute, Universita Popolare Europea (UPE) și Academia Italiană di Scienze dell’Investigazione, Criminalistiche e per la Sicurezza (AIICS) în care îți vor fi recunoscute competențele.';
        this.imgUrl = '/assets/img/results/Expert-app.png';
      } else if(this.score < 10 && this.score > 6) {
        this.title = 'SPECIALIST în decodificarea microexpresiilor';
        this.subtitle = 'Felicitări! Ai recunoscut majoritatea microexpresiilor!';
        this.text = 'Dacă vrei să îți perfecționezi abilitățile de decodificare și să afli informații noi despre limbajul nonverbal și interpretarea microexpresiilor, te invităm să participi la cusul  de formare FACS (Facial Action Coding System) susținut de Trainerul Internațional Dr. Igor Vitale, expert în analiza comportamentală și tehnici FACS. La final vei primi o Diplomă de Participare, sub egida Zivac Group, Internațional Applied Psychology Institute, Universita Popolare Europea (UPE) și Academia Italiană di Scienze dell’Investigazione, Criminalistiche e per la Sicurezza (AIICS) în care îți vor fi recunoscute competențele.';
        this.imgUrl = '/assets/img/results/Specialist-app.png';
      } else if(this.score < 7 && this.score > 3) {
        this.title = 'Decodificator INTUITIV al microexpresiilor';
        this.subtitle = 'Ai un ochi bun! Ai reușit să recunoști peste jumătate din emoțiile prezentate!';
        this.text = 'Dacă vrei să îți perfecționezi abilitățile de decodificare și să afli informații noi despre limbajul nonverbal și interpretarea microexpresiilor, te invităm să participi la cusul  de formare FACS (Facial Action Coding System) susținut de Trainerul Internațional Dr. Igor Vitale, expert în analiza comportamentală și tehnici FACS. La final vei primi o Diplomă de Participare, sub egida Zivac Group, Internațional Applied Psychology Institute, Universita Popolare Europea (UPE) și Academia Italiană di Scienze dell’Investigazione, Criminalistiche e per la Sicurezza (AIICS) în care îți vor fi recunoscute competențele.';
        this.imgUrl = '/assets/img/results/Intuitiv-app.png';
      } else if(this.score < 4 && this.score > 0) {
        this.title = 'Decodificator ÎNCEPĂTOR al microexpresiilor';
        this.subtitle = 'Ai recunoscut câteva emoții dar nu strică să mai exersezi!';
        this.text = 'Dacă vrei să îți construiești baza de cunoștințe în decodificarea și interpretarea microexpresiilor, să afli despre mecanismele limbajulului nonverbal și să îți îmbunătățești abilitățile de comunicare te invităm să participi la cusul  de formare FACS (Facial Action Coding System) susținut de Trainerul Internațional Dr. Igor Vitale, expert în analiza comportamentală și tehnici FACS. La final vei primi o Diplomă de Participare, sub egida Zivac Group, Internațional Applied Psychology Institute, Universita Popolare Europea (UPE) și Academia Italiană di Scienze dell’Investigazione, Criminalistiche e per la Sicurezza (AIICS) în care îți vor fi recunoscute competențele.';
        this.imgUrl = '/assets/img/results/Incepator-app.png';
      } else {
        this.title = 'Mai încearcă!';
        this.subtitle = 'Nu ai recunoscut nici o emoție dar poți învăța!';
        this.text = 'Dacă vrei să îți construiești baza de cunoștințe în decodificarea și interpretarea microexpresiilor, să afli despre mecanismele limbajulului nonverbal și să îți îmbunătățești abilitățile de comunicare te invităm să participi la cusul  de formare FACS (Facial Action Coding System) susținut de Trainerul Internațional Dr. Igor Vitale, expert în analiza comportamentală și tehnici FACS. La final vei primi o Diplomă de Participare, sub egida Zivac Group, Internațional Applied Psychology Institute, Universita Popolare Europea (UPE) și Academia Italiană di Scienze dell’Investigazione, Criminalistiche e per la Sicurezza (AIICS) în care îți vor fi recunoscute competențele.';
        this.imgUrl = '/assets/img/test.png';
      }
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }
}



