import { Component, OnInit } from '@angular/core';

import Question from '../shared/entities/question';
import Option from '../shared/entities/option';

import { QuestionService } from '../shared/services/question.service';

import { Router } from '@angular/router';

@Component({
  selector: 'quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.css'],
  providers: [QuestionService]
})
export class QuizComponent implements OnInit {
  questions: Question[];

  step: number;
  score: number;

  currentQuestion: Question;

  answeredSlide: boolean;
  finishedSlide: boolean;

  finishedQuiz: boolean;

  constructor(private questionService: QuestionService, private router: Router) {
    this.step = 0;
    this.score = 0;
  }

  ngOnInit(): void {
    this.questions = this.questionService.getQuestions().slice(0, 10);
    this.questionService.scrambleQuestions(this.questions);
    this.currentQuestion = this.questions[this.step];
  }

  stepUp(): void {
    if (this.finishedQuiz) {
      return;
    }

    if (++this.step >= this.questions.length) {
      this.finishedQuiz = true;
      this.router.navigate(['/bye', this.score]);

      return;
    }

    this.currentQuestion = this.questions[this.step];

    this.answeredSlide = false;
    this.finishedSlide = false;
  }

  optionSelected(option:Option): void {
    if (this.answeredSlide || this.finishedQuiz) {
      return;
    }

    if (this.currentQuestion.answer.id === option.id) {
      this.score++;
    }

    setTimeout(() => {
      this.answeredSlide = true;
      this.finishedSlide = true;
    }, 1500);
  }
}



