import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

import Option from '../../shared/entities/option';

@Component({
  selector: 'slide-options',
  templateUrl: './slide-options.component.html',
  styleUrls: ['./slide-options.component.css']
})
export class SlideOptionsComponent implements OnInit {
  @Input() options: Option[];
  @Input() correctOption: Option;

  displayOptions: any[];

  @Output() onSelectOption: EventEmitter<any> = new EventEmitter();

  ngOnInit(): void {
    this.displayOptions = this.options.map(o => {
      return {
        id: o.id,
        text: o.text,
        green: false,
        red: false
      }
    });
  }

  select(option): void {
    if (option.id === this.correctOption.id) {
      this.displayOptions = this.options.map(o => {
        if (o.id === option.id) {
          return {
            id: o.id,
            text: o.text,
            green: true,
            red: false
          }
        }

        return {
          id: o.id,
          text: o.text,
          green: false,
          red: false
        }
      });
    } else {
      this.displayOptions = this.options.map(o => {
        if (o.id === option.id && o.id !== this.correctOption.id) {
          return {
            id: o.id,
            text: o.text,
            green: false,
            red: true
          }
        } else if (o.id === this.correctOption.id) {
          return {
            id: o.id,
            text: o.text,
            green: true,
            red: false
          }
        }

        return {
          id: o.id,
          text: o.text,
          green: false,
          red: false
        }
      });
    }

    this.onSelectOption.emit(option);
  }
}



