import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';

import { EntryComponent } from './components/entry.component';
import { QuizComponent } from './components/quiz.component';
import { ExitComponent } from './components/exit.component';

import { SlideQuestionComponent } from './components/slide/slide-question.component';
import { SlideOptionsComponent } from './components/slide/slide-options.component';
import { SlideAnswerComponent } from './components/slide/slide-answer.component';

import { RouterModule, Routes } from '@angular/router';

import { QuestionService } from './shared/services/question.service';

const appRoutes: Routes = [
  { path: 'hi', component: EntryComponent },
  { path: 'quiz', component: QuizComponent },
  { path: 'bye/:score', component: ExitComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    EntryComponent,
    QuizComponent,
    ExitComponent,
    SlideQuestionComponent,
    SlideOptionsComponent,
    SlideAnswerComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(
      appRoutes,
    )
  ],
  providers: [QuestionService],
  bootstrap: [AppComponent]
})
export class AppModule { }



