import Option from './option'

export default class Question {
    text: string;
    options: Option[];

    answer: Option;

    mainImg: string;

    features: any[]

    constructor(
        text: string,
        options: Option[],
        answer: Option,
        mainImg: string,
        features: any[]) {
        this.text = text;
        this.options = options;
        this.answer = answer;
        this.mainImg = mainImg;
        this.features = features;
    }
}


